@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(-35deg);
            transform: rotate(-35deg);
  }
  to {
    -webkit-transform: rotate(35deg);
            transform: rotate(35deg);
  }

}


@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(-35deg);
            transform: rotate(-35deg);
  }
  to {
    -webkit-transform: rotate(35deg);
            transform: rotate(35deg);
  }

}


